import React, { useState, useEffect } from 'react'
import Grid from '@material-ui/core/Grid'
import Avatar from '@material-ui/core/Avatar'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import IconButton from '@material-ui/core/IconButton'
import NotificationsIcon from '@material-ui/icons/Notifications'
import ArrowDropDown from '@material-ui/icons/ArrowDropDown'
import { Typography } from '@material-ui/core'

import ConfigController2 from 'Controllers/ConfigController2'

// Styles
import styles from './HeaderComponent.module.scss'
import images from 'Constant/images/images'

export default function HeaderComponent ({ logout, username: user }) {
  const [anchorEl, setAnchorEl] = React.useState(null)
  const [username, setUsername] = useState(user)
  const [profilePic, setProfilePic] = useState('')
  useEffect(() => {
    setUsername(user)
  }, [user])

  useEffect(() => {
    ConfigController2.getUserManagementData()
    .then(({ profile_pic }) => {
      if(profile_pic !== 'DEFAULT_PIC') setProfilePic(profile_pic)
    })
  }, [])

  return (
    <Grid item container className={styles.root}>
      <Grid item className={styles['left-container']}>
        <img src={images.logo} alt='logo' />
      </Grid>

      <Grid item className={styles['right-container']}>
        <IconButton>
          <NotificationsIcon />
        </IconButton>
        
        <Avatar className={styles.avatar}>
          {profilePic ? <img alt='avatar' id="header-user-avatar" src ={`data:image/png;base64,${profilePic}`} width="100%"/> : null}
        </Avatar>
        
        <IconButton
          aria-controls='simple-menu'
          aria-haspopup='true'
          onClick={(event) => setAnchorEl(event.currentTarget)}
        >
          <ArrowDropDown />
        </IconButton>

        <Menu
          id='header-menu'
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          keepMounted
          onClose={() => setAnchorEl(null)}
        >
          <MenuItem id='logout' onClick={logout}>
            Logout
          </MenuItem>
        </Menu>

        <Typography align='center' variant='body2'>Welcome<br /> {username}</Typography>
      </Grid>

    </Grid>
  )
}
